import { FilterItem } from './types';

export const isListIncludesItem = (
  targetItem: FilterItem,
  items: FilterItem[]
): boolean => items.some(item => item.value === targetItem.value && item.filterId === targetItem.filterId);

export const getCurrentActiveItems = (
  items: FilterItem[],
  activeItems: FilterItem[]
): FilterItem[] => items.filter(item => isListIncludesItem(item, activeItems));

export const getFilterId = (currentItems: FilterItem[]): string | number => {
  const isEachItemIncludesFilterId = currentItems.every(item => !!item?.filterId);

  if (!isEachItemIncludesFilterId) {
    console.error('Each filter item at `FilterGroup` should have `filterId`: string | number');

    return '';
  }

  return currentItems[0]?.filterId ?? '';
};
